import { Paper, TableBody, TableCell, TableRow, TextField, Toolbar, withStyles, InputAdornment } from "@material-ui/core";
import React, { useState, useEffect, useRef } from "react";
import * as TActions from "../../../../../actions/Tax/tax";
// import AddTaxForm from "../../AddTax/AddTaxForm";
import { connect } from "react-redux";
import useTable from "../../useTable";
import Controls from "../../../controls/Controls";
import { EditOutlined, Search } from "@material-ui/icons";
import AddIcon from "@material-ui/icons/Add";
import PageHeader from "../../../PageHeader";
import CategoryTwoToneIcon from '@material-ui/icons/CategoryTwoTone';
import Popup from "../../../Popup";
import EditOutlinedIcon from "@material-ui/icons/EditOutlined";
import CloseIcon from "@material-ui/icons/Close";
import DeleteIcon from "@material-ui/icons/DeleteForeverSharp";
import { useToasts } from "react-toast-notifications";
import Notification from "../../../controls/Notification";
import ConfirmDialog from "../../../controls/ConfirmDialog";
import * as transActions from "../../../../../actions/Transactions/Transactions";
import * as accActions from "../../../../../actions/Accounts/Accounts";


const styles = theme => ({

    paper: {
        margin: theme.spacing(-8.9),
        padding: theme.spacing(6),

    },

    searchInput: {
        width: '75%'
    },

    newButton: {
        position: 'absolute',
        right: '10px '

    },

})

const headCells = [
    { id: 'transId', label: 'Transaction Id' },
    { id: 'transType', label: 'Transaction Name' },
    { id: 'amount', label: 'Amount' },
    { id: 'date', label: 'Date' },
    { id: 'Actions', label: 'Actions', disableSorting: true },
]

const ViewAllTransactions = ({ classes, ...props }) => {

    const [filterFn, setFilterFn] = useState({ fn: items => { return items; } })
    const [openPopup, setOpenPopup] = useState(false)
    const [currentId, setCurrentId] = useState(0)
    const { addToast } = useToasts()
    const { notify, setNotify } = useState({ isOpen: false, message: '', type: '' })
    const [confirmDialog, setConfirmDialog] = useState(false)
    const [deleteRecord, setDeleteRecord] = useState(false)

    const [accountNumber, setAccountNumber] = useState("");
    const [accountName, setAccountName] = useState("");
    const [accBalance, setAccBalance] = useState(0);
    const [invLimit, setInvLimit] = useState(0);
    const [dateCreated, setDateCreated] = useState("");
    const showToast = () => {
        addToast("Record was deleted succesffuly", { appearance: 'error' })
        addToast("Record was deleted succesffuly", { appearance: 'error' })
        addToast("Record was deleted succesffuly", { appearance: 'error' })

    }
    const onDelete = id => {
        props.DeleteTax(id, showToast)
        setConfirmDialog(false)

    }


    useEffect(() => {
        props.fetchAllTransactions();
        getAccount();
        // props.fetchTransactionsByAccNo(accountNumber);
      
    }, []);




    const {
        TblContainer,
        TblHead,
        TblPagination,
        recordsAfterPagingAndSorting,
    } = useTable(props.transList, headCells, filterFn)


    const handleSearch = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value == "")
                    return items;
                else {
                    return items.filter(x =>

                        x.transId.toLowerCase().includes(target.value.toLowerCase()) +
                        x.transType.toLowerCase().includes(target.value.toLowerCase()) +
                        x.accNo.toString().toLowerCase().includes(target.value.toLowerCase()))
                }

            }
        })
    }

    const addorEdit = (employee, resetForm) => {

        setOpenPopup(false)
        setNotify({
            isOpen: true,
            message: 'Submitted Successfully',
            type: 'Success'
        })
    }

    const openInPopup = record => {
        setCurrentId(record)
        setOpenPopup(true)
    }

    const openInDialog = () => {
        setConfirmDialog(true)
    }

    const getAccount = async () => {

        try {
            const response = await fetch('http://localhost:5008/api/Accounts/get-acc-by-uid/' + window.userId, {
                // method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                credentials: 'include',

            })
            const content = await response.json();

            setAccountNumber(content.accNo);
            setAccBalance(content.balance);
            setInvLimit(content.investmentLimit);
            setAccountName(window.name);
            setDateCreated(content.dateCreated);

        } catch {

        }
    }

    return (

        <>
           <PageHeader
                title="Transaction List"
                //subTitle="Form design with validation"
                icon={<CategoryTwoToneIcon fontSize="large" />}
            />,
            <Paper className={classes.paper} elevation={3}>

                <Toolbar>
                    <Controls.Input
                        className={classes.searchInput}
                        label="Search for Transaction"

                        InputProps={{
                            startAdornment: (<InputAdornment position="start">
                                <Search />
                            </InputAdornment>)
                        }}
                        onChange={handleSearch}

                    />
                    <Controls.Button
                        text="Add New"
                        variant="outlined"
                        startIcon={<AddIcon />}
                        className={classes.newButton}
                        onClick={() => { setOpenPopup(true); setCurrentId(0); }}
                    />
                </Toolbar>
                <table class="table .table-responsive{-sm|-md|-lg|-xl|-xxl} table-hover " >
                    <thead >


                    </thead>
                    <tbody>
                        <TblContainer >
                            <TblHead />
                            <TableBody>
                                {
                                    recordsAfterPagingAndSorting().map((record, index) => {
                                        return (<TableRow key={index} hover>
                                            <TableCell>{record.transId}</TableCell>
                                            <TableCell>{record.transType}</TableCell>
                                            <TableCell>{record.amount}</TableCell>
                                            <TableCell>{record.date}</TableCell>

                                            <TableCell>
                                                <Controls.ActionButton
                                                    color="primary"
                                                    // onClick={()=>{setCurrentId(record.id)}}
                                                    onClick={() => { openInPopup(record.transId) }}
                                                >
                                                    <EditOutlinedIcon fontSize="small" />
                                                </Controls.ActionButton>
                                                <Controls.ActionButton
                                                    color="secondary"
                                                    onClick={() => {
                                                        openInDialog()
                                                        setConfirmDialog({
                                                            onConfirm: () => { onDelete(record.transId) }
                                                        })

                                                        //onDelete(record.id)
                                                    }
                                                    }
                                                >
                                                    <DeleteIcon fontSize="small" />
                                                </Controls.ActionButton>
                                            </TableCell>
                                        </TableRow>

                                        )
                                    })
                                }

                            </TableBody>
                        </TblContainer>


                    </tbody>
                </table>

                <TblPagination />
            </Paper> 
            <Popup
                title="Transaction Form"
                openPopup={openPopup}
                setOpenPopup={setOpenPopup}
            > 
                {/* <AddTaxForm
                    {...({ currentId, setCurrentId, openPopup, setOpenPopup })}
                addorEdit={addorEdit}
                /> */}
            </Popup>
            {/* <Notification
                notify={notify}
                setNotify={setNotify}
            />
            <ConfirmDialog
                title="Are you sure you want to delete this record?"
                subTitle="This operation can't be reversed"
                confirmDialog={confirmDialog}
                setConfirmDialog={setConfirmDialog}
            /> */}
        </>
        // {...({ currentId, setCurrentId }) }

    )
}

const mapStateToProps = state => ({
    transList: state.transactions.list

})

const mapActionToProps = {
    fetchAllTransactions:transActions.fetchAllTransactions,
    createTransaction: transActions.createTransaction,
    fetchTransactionsByAccNo: transActions.fetchTransactionsByAccNo,
    updateAccount: accActions.updateAccount

}


export default connect(mapStateToProps, mapActionToProps)(withStyles(styles)(ViewAllTransactions));